@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');
*{
    font-family: "Inter", sans-serif;
}
body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #dce0e3;
}

@keyframes fade-up {
    0% {
    opacity: 0;
    transform: translateY(30px) scale(0.9);
    }
    100% {
    opacity: 1;
    transform: translateY(0px) scale(1);
    }
}

:root {
    --clr-main: #007bff; /* Example main color */
    --clr-light: #ffffff; /* Example light color */
    --clr-neutral: #f0f0f0; /* Example neutral color */
    --clr-dark: #333333; /* Example dark color */
  }

.intro{
    animation:fade-up 0.3s;
}

.container{
  background: #EDF0F6;
  background: linear-gradient(354deg, #137DF5 0%, #14B1F8 100%);
  width: 320px;
  height: 550px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  border-radius: 10px;
}

.date{
    font-size: 15px;
    color: #dfe2e7;
    font-weight: 500;
    margin-top: 15%;
}

.city{
    color:white;
}

.temp{
    color:white;
    font-size: 3.5rem;
    margin-top: 10px;
    margin-bottom: 10px;

}

.info{
    color:white;
    font-size: 1rem;
}


form{
    margin-top: 30px;
}

input{
    padding: 1rem;
    padding-right: 14rem;
    border: 2px solid;
    border-color: white;
    border-radius: 7px 0 0 7px;
    background-color: #ffffff; 
}

::-webkit-input-placeholder{
    font-weight: bold;
    color:rgb(0, 0, 0);
}

.get_loc{
    margin-top: 8px;
    padding: 0.7rem;
    padding-left:4rem;
    padding-right:4rem;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    border: none;
    color:white;
    border-color: white;
    border-radius: 7px;
    font-weight: bolder;
    background-color: #1a416b;
    font-size: 15px;
    transition: 0.1s ease-in;
}
.get_loc:hover{
    background-color:#092849 ;
}
.loc{
    padding: 1rem;    
    background-color: #ffffff;
    border: none;
    color:white;
    background-color: #c5b3b3;
    border-radius: 0 7px 7px 0;
}
.arrow {
    border: solid rgb(255, 255, 255);
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
  }
  
  .right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }
.loc_img{
    width: 15px;
    height: 15px;
}
  
    .modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden;
    transform: scale(1.1);
    transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
  }
  
  .modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(225, 219, 219);
    padding: 2rem 2.5rem;
    width: 30em;
    border-radius: 0.5rem;
  }
  
  .close-button {
    float: right;
    width: 1.5rem;
    font-size: 1.2em;
    line-height: 1;
    padding: 0 .2em .15em;
    text-align: center;
    cursor: pointer;
    border-radius: 0.25rem;
    background-color: var(--clr-neutral);
    color: var(--clr-dark);
    transition: color 0.12s ease-in-out;
  }
  
  .close-button:hover {
    color: var(--clr-main);
  }
  
  .show-modal {
    opacity: 1;
    visibility: visible;
    transform: scale(1.0);
    transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
  }
  .header{
    margin: 20px;
    width: 90rem;
    height: 100px;
    display: flex;    
}

.logo{
    position: fixed;
}

nav{
   padding-left: 25rem;
}

nav ul li a{
    padding:20px;
    color:black;
    border-radius: 0.5rem;
    border:1rem;
    transition: 0.2s linear;
}
.social{
    list-style-type: none;
    display: flex;
    margin-left: 81rem;
}

.social li{
    margin-top:10px;
    padding: 10px;
}

.side-logo{
  height: 35px;
  width: 35px;
}


.footer{
  margin-top: 37rem;
  align-items: center;
  text-align: center;
}

.humi{
  font-size: 1.5rem;
  list-style-type: none;
  margin-left: 5rem;
  display: flex;
}
.wind{
  font-size: 1.5rem;
  list-style-type: none;
  margin-left: 5rem;
  display: flex;
}